@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
    color: #F7B633;
    letter-spacing: 10px;
    font-weight: 700;
    text-align: left;
    padding-top: 68px;
    
}

.card {
    margin-top: 100px;
    border-radius:5px;
    /* border-color: #F7B633; */
    /* border-style: solid; */
    border: 2px solid #F7B633;
    /* box-shadow: 5px 5px 10px  rgba(247, 182, 51, 0.5); */
    padding:15%;
    padding-left: 8%;
    height: 400px;
    margin-top: 0px;
}

.card-title {
    color: #F7B633;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 6px;
}

.card-text {
    padding-top: 10px;
    color: #666666;
}

.title-row {
    margin-top: 0px;
    /* margin-bottom: 80px; */
    height: 20px; 
    color: #666666;
    text-align: center;
}

.features-title {
    color: #F7B633;
    font-size: 22px;
    text-align: center;
    margin-bottom: 40px;
    margin-top: 0px;
}


.catch-line {
    font-size: 18px;
    color: #F7B633;
}

.subtitle {
    font-size: 16px;
    color: #666666;
}



.container {
    font-family: 'Roboto', sans-serif
}

h2 {
    color: #F7B633;
    letter-spacing: 8px;
    font-weight: 700;
    text-align: left;
    padding-top: 68px;
    
}

h3 {
    color: #666666;
    margin-bottom: 0;
    font-weight: 700
}

p {
    color: #666666;
    font-size: 16px;
    font-weight: 400;
    padding-top: 30px;
    line-height: 1.6;
    margin-right: 30px;
    margin-bottom: 10px
}

img{
    padding-top: 30px;
    width: 100%
}

a {
    color: #F7B633;;
    font-weight: 500
}
nav.navbar, .navbar-default {
  background-color: #F7B633 !important;
}

.navbar-default {
  background-image: none;
  border: none;
  border-radius: 0;
  font-family:'Roboto', sans-serif;
  font-size: 16px;
  padding-top: 40px;
}

.navbar-default .navbar-nav > li > a {
  color: white;
  font-weight: 600;
}

.navbar-default .navbar-brand {
  color: white;
  font-weight: 600;
  letter-spacing: 0.5em;
}

.navbar-default .navbar-toggle {
  border-color: white;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #A3A3A3;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background-color: white;
}

.navbar-default .navbar-brand:focus, .navbar-default .navbar-brand:hover {
  color: white;
}

.navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover {
  color: '#022C43';
  background-color: transparent;
}

.navbar-brand > p {
  line-height: 16px;
  padding-top: 0px;
  margin-top: 0px;
  font-weight: 600;
  font-size: 18px
}
footer {
    padding-top: 100px;
    text-align: center;
    line-height: 30px;
    padding-bottom: 100px;
    color: gray;
}
